import reviews from './2021/review.json';
import { ReviewModel } from './types';

export const getAllReviews = () => {
    return reviews;
}

export const getReview = (postId: string): ReviewModel | undefined => {
    try {
        return reviews.find((_p: ReviewModel)=>{
            return _p.id == postId
        })
    } catch (error) {
        return
    }
}