












import {
    Component,
    Prop,
    Vue
} from "vue-property-decorator";
import {
    ReviewModel
} from '../../../posts/types';

@Component
export default class CardReview extends Vue {
    @Prop() review!: ReviewModel | undefined
    title = "L'insostenibile leggerezza dell'essere"
    content = "Per due punti, nascita e morte, passano infinite rette. O forse no, ne passa solo una"
    postUrl = "/review/1"
    constructor() {
        super();
        if (this.review) {
            this.title = this.review.title;
            this.content = this.review.subTitle ? this.review.subTitle : "";
            this.postUrl = this.postUrl + this.review.id;
        }
    }
}
