import Vue from 'vue'
import VueRouter, { RouteConfig } from 'vue-router'
import HomeView from '@/components/views/HomeView.vue'
import PostView from '@/components/views/post/PostView.vue'
import PostListView from '@/components/views/post/PostListView.vue'
import BookReviewView from '@/components/views/review/BookReviewView.vue'
import BookReviewListView from '@/components/views/review/BookReviewListView.vue'
Vue.use(VueRouter)

const routes: Array<RouteConfig> = [
  {
    path: '/',
    component: Vue.extend(HomeView as any)
  },
  {
    path: '/post/',
    component: Vue.extend(PostListView as any),
    props: true
  },
  {
    path: '/post/:post_id',
    component: Vue.extend(PostView as any),
    props: true
  },
  {
    path: '/review/',
    component: Vue.extend(BookReviewListView as any),
    props: true
  },
  {
    path: '/review/:review_id',
    component: Vue.extend(BookReviewView as any),
    props: true
  },
  {
    path: '/*',
    component: Vue.extend(HomeView as any)
  }
  
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
