






import { getPost } from "@/posts/loader";
import {
    Component,
    Vue,
    Prop
} from "vue-property-decorator";

import Post from '../../posts/Post.vue'
import { PostI } from '../../../posts/types';

@Component({
    components: {
        "Post": Vue.extend(Post as any)
    }
})
export default class PostView extends Vue {
    @Prop() post_id!: string
    post: PostI | undefined
    constructor() {
        super()
        const post = getPost(this.post_id);
        this.post = post;
    }

    mounted() {
        try {
            const post = getPost(this.post_id);
            this.post = post;

        } catch (error) {
            console.log(error)
        }
    }
}
