
import first from './2021/first.json';
import { PostI } from './types';

export const getFirstPost = (): PostI => {
    return first[0];
}

export const getAll = (): PostI[] => {
    return first;
}

export const getPost = (postId: string): PostI | undefined => {
    try {
        return first.find((_p: PostI)=>{
            return _p.id == postId
        })
    } catch (error) {
        return
    }
}