












import {
    Component,
    Vue,
    Prop
} from "vue-property-decorator";
import SubTitle from './SubTitle.vue';
import Title from './Title.vue';
import Body from './Body.vue';

import {
    PostI
} from "@/posts/types";
const c: any = {
        SubTitle: SubTitle,
        Title: Title,
        Body: Body
    }
@Component({
    components: c
})
export default class Post extends Vue {

    @Prop() post!: PostI | undefined

    title = "Ciao";
    subtitle = "ciao piccolo";
    content = "contenuto di ciao"

    constructor() {
        super();
        try {
            const post: PostI | undefined = this.post;
            console.log("post", post)
            if (post) {
                this.title = post.title;
                this.subtitle = post.subTitle;
                this.content = post.body.paragraph[0].content
            }

        } catch (error) {
            console.log("Error", error)
        }
    }
}
