




















import {
    Component,
    Prop,
    Vue
} from "vue-property-decorator";
import { PostI } from '../../../posts/types';

@Component
export default class Card extends Vue {
    @Prop() post!: PostI
    title = ""
    content = ""
    postUrl = "/post/"
    constructor() {
        super();
        this.title = this.post.title;
        this.content = this.post.subTitle ? this.post.subTitle : "";
        this.postUrl = this.postUrl + this.post.id;
    }
}
